function MessageList(props) {
    return(
        <div>
            {props.messages.map((item, index) => {
                return(
                    <div className='message-item' key={index}>
                        <p>{item.user}</p>
                        <h6>{item.message}</h6>
                    </div>
                )
            })}
        </div>
    )
}

export default MessageList;