import logo from './logo.svg';
import './App.css';
import MessageList from './Components/MessageList/MessageList.js'
import Login from './Components/Login/Login.js'
import { useState, useEffect } from 'react'
import axios from 'axios'
import { io } from "socket.io-client";

const MessagesUrl = '/messages'

function App() {
  const [messages, setMessages] = useState([])
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [username, setUsername] = useState("")
  const [messageValue, setMessageValue] = useState("")

  const socket = io(/*'https://localhost:4000', {transports: ['websocket']}*/)

  socket.on("NewMessage", data => {
    console.log('New message')
    setMessages(data)
  })

  useEffect(() => {
    axios.get(MessagesUrl).then((res) => {
      console.log(res.data)
      setMessages(res.data)
    })
  }, [])

  function handleClick() {
    socket.emit('NewMessage', {message: messageValue, user: username})
  }

  return (
    <>
      { isLoggedIn ? (
        <div>
          <MessageList messages={messages} />
          <input type="text" placeholder="Write a message" value={messageValue} onChange={(e) => setMessageValue(e.target.value) } />
          <button onClick={handleClick}>Send!</button>
        </div>
      ) :  (
        <Login setIsLoggedIn={setIsLoggedIn} />
      )}
    </>
  );
}

export default App;
